.steps-container {
    display: block;
    width: 75%;
    max-width: 500px;
    min-height: 20px;
    margin-top: 25px;
    margin-right: auto;
    margin-left: auto;
}

.steps-container .step-line {
    display: inline-block;
    width: 33.3333%;
    height: 2px;
    margin-bottom: 11px;
    background-color: #fff;
}

.steps-container .step {
    display: inline-block;
    width: 25px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 50px;
    cursor: pointer;
}

.steps-container .step.active {
    background-color: #fff;
}

.steps-row {
    display: block;
    width: 75%;
    max-width: 600px;
    min-height: 20px;
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
}

.steps-row .step-title {
    display: inline-block;
    margin-right: 57px;
    margin-left: 57px;
    font-size: 10px;
    letter-spacing: 2px;
    text-transform: uppercase;
}


.pay-options-container .flex.item {
    padding: 15px 0;
    border-bottom: 2px solid #eee;
    text-align: left;
    -ms-flex-align: center;
        align-items: center;
}

.pay-options-container .flex.item div {
    margin-left: 15px;
}

.pay-options-container .flex.item div img {
    width: 30px;
    height: 30px;
}

.pay-options-container h5 {
    color: #999;
    font-size: 20px;
    margin: 10px 0;
}

.card-info-container {
    margin-top: 50px;
}

.checkout-details-container p {
    color: #aaa;
    font-size: 12px;
}

.checkout-details-container h5 {
    font-size: 18px;
}

.checkout-details-container .address-txt {
    font-size: 14px;
    color: #333;
    font-weight: 600;
}

.thank-you-container .inner {
    max-width: 500px;
}


